<template>
    <!-- 订单详情关闭订单信息 -->
    <div class="close-info">
        <div class="title">{{ statusinfo.title }}</div>

        <div class="content">

            <div class="item">
                <div class="item-left">
                    <span>操作人</span>
                </div>
                <div class="item-right">
                    <span>{{ statusinfo.name  }}</span>
                </div>
            </div>

            <div class="item">
                <div class="item-left">
                    <span>{{statusinfo.label}}时间</span>
                </div>
                <div class="item-right">
                    <span>{{ statusinfo.time  }}</span>
                </div>
            </div>

            <div class="item">
                <div class="item-left reason">
                    <span>{{statusinfo.label}}原因</span>
                </div>
                <div class="item-right">
                    <span>{{ statusinfo.reason  }}</span>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
export default {
  props: {
    statusinfo: {
        type: Object
    }
  },
  data() {
    return {
      operater: "xxx",
      time: "ssss",
      reason: "原因"
    };
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

.title {
  @include fc(14px,#666);
  margin: 20px 0;
  font-weight: bold;
}
.content {
  border: 1px solid #d8d8d8;
  padding-top: 20px;
  width: 100%;
}
.item {
  margin-bottom: 20px;

  .item-left {
    @include fc(14px,#000);
    display: inline-block;
    width: 106px;
    opacity: 0.5708;
    text-align: right;
  }
  .item-right {
    @include fc(14px,#333);
    display: inline-block;
    padding-left: 14px;
    max-width: 80%;
  }
  .reason {
    vertical-align: top;
  }
}
</style>




